import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import linkState from 'linkstate';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_BOTTOM = mu.marginBottom();

import Error from '../../components/error';
import Message from '../../components/message';
import InviteFriend from '../../components/user/invite';
import User from '../../components/user';
import AdminNotification from '../../components/admin/notification';

@observer
class UserNotifications extends Component {
	constructor(props) {
        super(props);
        this.state = {
            time: Date.now(),
			count: 10
        };
    }
    async loadAll() {
        await this.loadNotifications();
        await this.markNotificationsAsRead();
    }

    async loadNotifications() {
        const { userStore } = this.props.stores;
        await userStore.getNotifications();
    }

    async markNotificationsAsRead() {
        const { userStore } = this.props.stores;
        const { notifications } = userStore;
        const ids = notifications.map(e => e.id);
        await userStore.markNotificationsAsRead({ ids });
    }

	search = async () => {
        const { userStore } = this.props.stores;
        const { searchText } = this.state;
        await userStore.searchUsers({ search: searchText });
    }

    toggleDate = (e) => {
        e.stopPropagation();
        e.preventDefault();
        const { showDate } = this.state;
        this.setState({ showDate: !showDate });
    }

	componentDidMount() {
        this.loadAll();
	}

	render() {
        const { showDate } = this.state;
		const { userStore } = this.props.stores;
		const { notifications } = userStore;
		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
                <div class='row'>
                    {notifications && notifications.map(notification => <AdminNotification notification={notification} showDate={showDate} />)}
                </div>
            </div>
        );
	}
}

export default UserNotifications;
